/**********************************
GENERAL
***********************************/
@font-face {
  font-family: 'NeutraText';
  src: url("/shared/NeutraText-DemiItalic.woff") format("woff2"), url("/shared/NeutraText-DemiItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
}


@font-face {
  font-family: 'NeutraText';
  src: url("/shared/NeutraText-Book.woff") format("woff2"), url("/shared/NeutraText-Book.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}


@font-face {
  font-family: 'NeutraText';
  src: url("/shared/NeutraText-Demi.woff") format("woff2"), url("/shared/NeutraText-Demi.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}


body {
  font-family: NeutraText;
  font-style: normal;
  font-size: 1em;
  line-height: 1em;
  background-color: #fff;
  color: #000;
}

#wrapper {
  max-width: 940px;
  margin: 0 auto;
  padding: 0 5%;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: #000;
  &:link, &:visited {
    color: #000;
  }
  &:hover {
    color: #000;
  }
  &:active {
    color: #000;
  }
}

.back-button {
  text-align: left;
  font-size: 12px;
  padding: 0px 0px 0px 0px;
}

/**********************************
MIX-INs
***********************************/

@mixin text-decoration-style($value) {
  @include prefixer(text-decoration-style, $value, moz webkit);
}

/**********************************
ANIMATIONS
***********************************/

@-webkit-keyframes {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@-moz-keyframes play-fw {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@-ms-keyframes play-fw {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@-o-keyframes play-fw {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@keyframes play-fw {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@-webkit-keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }

}
@-moz-keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }
}

@-ms-keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }
}

@-o-keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }
}

@keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }
}
/**********************************
HEADER
***********************************/

#siteheader {
  width: 100%;
  background-color: #fff;
  margin-right: auto;
  margin-left: auto;
  //transition: color .2s,background-color .2s;

.topRow-4 {
  position: relative;
  min-height: 1px;
  padding: 5px 15px 0 15px;
  }

.dropnav  {
  position: absolute;
  top: 10px;
  left: 15px;
  float:left;
  width: 28px;
  height: 28px;
  padding: 10px;
  cursor: pointer;

    .dropnavIcon {
      display: inline;
      width: 28px;
      height: 28px;
      position: absolute;
      text-align: center;
      background-size: auto 100%;
      margin: auto;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      background-image: url(/images/burger_blk.png);
    }
  }

  .dropnavOpen  {
  position: absolute;
  top: 10px;
  left: 15px;
  float:left;
  width: 28px;
  height: 28px;
  padding: 10px;
  cursor: pointer;


    .dropnavIcon {
       display: inline;
      width: 28px;
      height: 28px;
      position: absolute;
      text-align: center;
      background-size: auto 100%;
      margin: auto;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      background-image: url(/images/burger_blk.png);
      -webkit-animation:play-fw .25s steps(7) forwards;
      -moz-animation:play-fw .25s steps(7) forwards;
      -ms-animation:play-fw .25s steps(7) forwards;
      -o-animation:play-fw .25s steps(7) forwards;
      animation:play-fw .25s steps(7) forwards;

    }
  }

  .dropnavClosed {
  position: absolute;
  top: 10px;
  left: 15px;
  float:left;
  width: 28px;
  height: 28px;
  padding: 10px;
  cursor: pointer;

    .dropnavIcon {
      display: inline;
      width: 28px;
      height: 28px;
      position: absolute;
      text-align: center;
      background-size: auto 100%;
      margin: auto;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      background-image: url(/images/burger_blk.png);
      -webkit-animation:play-bw .25s steps(7) forwards;
      -moz-animation:play-bw .25s steps(7) forwards;
      -ms-animation:play-bw .25s steps(7) forwards;
      -o-animation:play-bw .25s steps(7) forwards;
      animation:play-bw .25s steps(7) forwards;

  }
}

