.topRow-8
{
  position:relative;
  min-height: 1px;
  padding: 0 15px;

  #dropnavMenu 
  {
   
    display:none;
    box-sizing: border-box;
    //margin: 0 auto;
    margin: 20px auto 0 auto;
     //z-index:1;


      ul 
      {
        display: block;
        width:100%;
        //overflow: hidden;
        padding: 1em 0 0 0;
        text-align: center;
        position:relative;
        list-style-type: none;
        color: #fff;
        -webkit-margin-before: 0;
        -webkit-padding-start: 0;
        -webkit-margin-after: 0;

        li.mobile
        {
          padding: 0;
          width: 100%;
          display: block;
          font-size: 1.3em;
          font-weight: bold;
          margin: .5em auto 0 auto;
          color: #000;
          text-align: center;
          //transition: all .3s ease-in-out;
          border:0;
          &:hover 
          {

          }
        }

        li::after 
        {
        content: "";
        display:block;
        width: 100px;
        margin: .3em auto 0 auto;
        text-align: center;
        height:1px;
        background:#fff;
        border-bottom:2px solid #000;
        }

        a 
        {
          text-decoration: none;
          color: #000;
          &:hover 
          {
            color: #767676;
            text-decoration: none;
          }
        }
      
      }
  }
}


