/******************
MAIN CONTENT
******************/

#mainColumn {
    margin: 0 auto;
    padding: 1.5em 2.5% 0 2.5%;
    width: auto;

 
  .previewItem {
    width: 100%;
    display: block;
    float:left;
    position:relative;
    margin: 0;
    padding: 30px;
    -webkit-box-sizing: border-box;
    vertical-align: baseline;
  }
 .first 
  {
  padding-top: 0px;
  }
  .previewItem span {
    vertical-align: baseline;
    float: left;
    bottom: 0px;
    font-size: 1em;
  }

}

.smallBorder {
  border-color: #8d8d8d;
  border-style: solid;
  border-width: 1px;
}


/******************
ABOUT
******************/

#contactColumn {
    margin: 30px auto 0 auto;
    padding: 40px 0 0 2.5%;
    width: auto;

  .contactItem {
    width: 95%;
    display: block;
    float:left;
    position:relative;
    margin: 0;
    padding: 0 2.5%;
    clear:left;

    p {
    margin: 0;
    padding:0;
    line-height: 1.4em;
    }

    span.blocky {
      display: block;
      margin: 15px 0 0 0;
    }

    span.bold {
      font-weight: bold;
    }
    
  }

  a {
      text-decoration: none;
      border-bottom: 1px dotted;
    }

  .primary {
      display: block;
      float: left;

    }

  .secondary {
      clear: left;
      width: 100%;
      float: left;
      margin: 15px 0 0 0;
      clear:left;
    }
    
}


/******************
FOOTER
